import { useEffect } from "react";
import { useNavigate } from "react-router-dom";

/**
 * Hook pour protger un niveau selon la progression stocke dans localStorage.
 * @param {number} levelNumber - Numro du niveau courant
 */
export function useLevelProtection(levelNumber) {
  const navigate = useNavigate();

  useEffect(() => {
    const currentLevel = parseInt(localStorage.getItem("currentLevel") || "1");
    if (currentLevel < levelNumber) {
      // renvoie au dernier niveau valid
      navigate(`/level${currentLevel}`);
    }
  }, [levelNumber, navigate]);
}
